import React, { Component } from "react";
import mainImage1 from "../../images/Main/mainImage1.webp"
import mainImage2 from "../../images/Main/mainImage2.webp"
import mainImage3 from "../../images/Main/mainImage3.webp"
import "./Main.scss"

class Main extends Component {
  render(){
    return(
      <main className="main container-fluid p-0">
        <div id="carouselExampleInterval" className="carousel slide" data-bs-ride="carousel">
          <div className="carousel-inner">
            <div className="carousel-item active" data-bs-interval="2000">
              <img loading="lazy" src={mainImage1} className="d-block w-100" alt="mainImage1"/>
              <div className="carousel-caption mainText d-none d-md-block pb-5">
                <h2 className="mainTitle mb-5 pb-5 ps-5 fs-1">WELLNESS</h2>
                <h3 className="mainSubtitle mb-4 pt-5">Welcome</h3>
                <h4 className="mainSubtitle2 mb-4 fs-2">Make a Amazing Look</h4>
                <p className="mb-5 pb-5">Lorem ipsum dolor sit</p>
              </div>
            </div>
            <div className="carousel-item" data-bs-interval="2000">
              <img loading="lazy" src={mainImage2} className="d-block w-100" alt="mainImage2"/>
              <div className="carousel-caption mainText d-none d-md-block">
                <h2 className="mainTitle mb-5 pb-5 ps-5 fs-1">WELLNESS</h2>
                <h3 className="mainSubtitle mb-4 pt-5">Welcome</h3>
                <h4 className="mainSubtitle2 mb-4 fs-2">Wellness Body Massage</h4>
                <p className="mb-5 pb-5">Lorem ipsum dolor sit</p>
              </div>
            </div>
            <div loading="lazy" className="carousel-item" data-bs-interval="2000">
              <img src={mainImage3} className="d-block w-100" alt="mainImage3"/>
              <div className="carousel-caption mainText d-none d-md-block">
                <h2 className="mainTitle mb-5 pb-5 ps-5 fs-1">WELLNESS</h2>
                <h3 className="mainSubtitle mb-4 pt-5">Welcome</h3>
                <h4 className="mainSubtitle2 mb-4 fs-2">Best Beauty And Spa</h4>
                <p className="mb-5 pb-5">Lorem ipsum dolor sit</p>
              </div>
            </div>
          </div>
          <button className="carousel-control-prev" type="button" data-bs-target="#carouselExampleInterval" data-bs-slide="prev">
            <span className="carousel-control-prev-icon" aria-hidden="true"></span>
            <span className="visually-hidden">Previous</span>
          </button>
          <button className="carousel-control-next" type="button" data-bs-target="#carouselExampleInterval" data-bs-slide="next">
              <span className="carousel-control-next-icon" aria-hidden="true"></span>
              <span className="visually-hidden">Next</span>
          </button>
        </div>
      </main>
    )
  }
}

export default Main