import "./Contact.scss"
import React, { Component } from "react"
import Input from "../../componentes/ControlledForm/Input"
import mapImage from "../../images/Contact/map.webp"

class Contact extends Component {
  handleSubmit = (event) => {
      event.preventDefault()
  }
  render() {
    return (
      <div className="contact container my-4">
        <h2 className="contactTitle text-center fs-1">CONTACT</h2>
        <div className="contacContenide row">
          <div className="contactMap col-12 col-sm-6 ">
            <img loading="lazy" className="contactImage img-fluid" src={mapImage} alt="imagen"/>
          </div>
          <form className="contactForm col-12 col-sm-6 " onSubmit={this.handleSubmit}>
            <h3 className="contactSubtitle fs-5">Vivamus lacinia odio ut Vivamus lacinia odiout lacinia odio ut</h3>
            <Input type="text" placeholder="Nombre" />
            <Input type="email" placeholder="Email" />
            <Input type="tel" placeholder="Phone" />
            <button type="submit" className="btn btn-primary">Send</button>
          </form>
        </div>
      </div>
    )
  }
}

export default Contact