import "./Gallery.scss"
import React from "react"
import GalleryCard from "../../componentes/GalleryCard/GalleryCard"

class Gallery extends React.Component {
  state = {
    galleryData: [],
    cargando: true
  }

  componentDidMount() {
    fetch('https://my-json-server.typicode.com/Luis923/json-db/gallery')
      .then(res => res.json())
      .then(galleryData => this.setState({ galleryData, cargando: false }))
      .catch(error => {
        // Manejo del error
      })
  }

  render() {
    if (this.state.cargando) {
      return <h1>Cargando...</h1>
    }

    return (
      <div className="gallery pb-5">
        <h2 className="galleryTitle text-center my-3 fs-1">GALLERY</h2>
        <div className="container">
          <div className="row gx-5 gy-4">
            {this.state.galleryData.map(galleryDat => (
              <div className="col-12 col-sm-6 col-md-4 col-lg-3 col-xl-3" key={galleryDat.id}>
                <GalleryCard
                  urlImagen={galleryDat.url}
                  subtitulo={galleryDat.subtitulo}
                  parrafo={galleryDat.parrafo}
                />
              </div>
            ))}
          </div>
        </div>
      </div>
    )
  }
}

export default Gallery